import React from "react";
import PropTypes from "prop-types";
import { Modal, Button } from "antd";

const FaqModal = ({ open, handleClose }) => {
  const footer = (
    <Button type="primary" onClick={handleClose}>
      Ok
    </Button>
  );

  return (
    <Modal title="FAQ" visible={open} onOk={handleClose} footer={footer}>
      <ul>
        <li>
          <h3>Czy obowizuj mnie jakie zniki?</h3>
          <p>
            Wszystkie aktualnie obowizujce oferty specjalne znajduj si w
            cenniku.
          </p>
        </li>
        <li>
          <h3>Czy mog wypoyczy u Was rcznik?</h3>
          <p>Nie prowadzimy wypoyczalni rcznikw.</p>
        </li>
        <li>
          <h3>Czy mog wjecha z wzkiem z dzieckiem na stref basenw?</h3>
          <p>
            Ze wzgldu na wilgotno i wysok temperatur jest to bardzo zy
            pomys, warunki te maj negatywny wpyw na stan techniczny sprztu.
          </p>
        </li>
        <li>
          <h3>Czy musz mie klapki i czepek?</h3>
          <p>
            Nie jest to wymagane, ale ze wzgldu na higien bardzo wskazane.
          </p>
        </li>
        <li>
          <h3>Czy s atrakcje z ktrych nie mona korzysta bdc mokrym?</h3>
          <p>
            Tak, korzystanie z niektrych saun wymaga bycia suchym, aby nie byo
            to niebezpieczne dla zdrowia.
          </p>
        </li>
      </ul>
    </Modal>
  );
};
export default FaqModal;

FaqModal.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired
};
