import React from "react";
import PropTypes from "prop-types";
import { Modal, Button } from "antd";

const AgreementsModal = ({ open, handleClose }) => {
  const footer = (
    <Button type="primary" onClick={handleClose}>
      Ok
    </Button>
  );

  return (
    <Modal title="REGULAMIN" visible={open} onOk={handleClose} footer={footer}>
      <ul>
        <li>
          <p>
            Niniejszy regulamin suy zachowaniu bezpieczestwa, porzdku i
            czystoci i obowizuje wszystkich klientw przebywajcych na jego
            terenie.
          </p>
        </li>
        <li>
          <p>
            Osoby znajdujce si na terenie Aquaparku s zobowizane
            podporzdkowa si instrukcjom oraz poleceniom ratownikw wodnych
            penicych dyur oraz innym pracownikw pywalni.
          </p>
        </li>
        <li>
          <p>
            Na terenie Obiektu znajduje si system monitoringu wizyjnego wraz z
            zapisem. Nagrania z kamer mog by podstaw do pocignicia do
            odpowiedzialnoci w stosunku do uytkownikw, ktrzy nie stosuj si
            do regulaminu oraz instrukcji. Kierownictwo Obiektu gwarantuje, i
            monitoringpowadzony jest w sposb nienaruszajcy dbr osobistych
            oraz godnoci uytkownikw.
          </p>
        </li>
        <li>
          <p>
            Kierownictwo obiektu moe czasowo ograniczy wstp do Aquaparku ze
            wzgldu na przekroczenie maksymalnej liczby osb korzystajcych.
          </p>
        </li>
        <li>
          <p>Zmiana ubrania na strj basenowy odbywa si w przebieralniach.</p>
        </li>
        <li>
          <p>
            Za rzeczy pozostawione bez nadzoru na terenie Obiektu, kierownictwo
            Obiektu nie odpowiada.
          </p>
        </li>
        <li>
          <p>
            Na terenie hali basenowej obowizuje wycznie czysty strj
            kpielowy, speniajcy wymogi higieniczne oraz estetyczne,
            nieposiadajcy zamkw byskawicznych albo innych wstawek metalowych
            lub plastikowych, mogcych stanowi zagroenie dla zdrowia lub
            bezpieczestwa korzystajcego jak rwnie by przyczyn uszkodze
            elementw wyposaenia Aquaparku
          </p>
        </li>
        <li>
          <p>
            Ratownikami s osoby noszce te koszulki z napisem RATOWNIK oraz
            czerwone spodenki, kadra kierownicza  koszulki biae
          </p>
        </li>
        <li>
          <p>
            Pomieszczenie centrum zarzdzania sub ratownicz znajduje si po
            lewej stronie od wejcia z szatni na hal basenow. Osoby
            potrzebujce doranej pomocy lub opiekunowie poszukujcy dzieci
            proszeni s skierowanie si do tego punktu.
          </p>
        </li>
        <li>
          <p>
            Ze wzgldu na zapewnienie zasad higieny i bezpieczestwa osobom
            znajdujcym si na terenie obiektu nie wolno:
            <br />
            a. wchodzi do wody wbrew zakazowi ratownikw, przy braku dyuru
            suby ratowniczej oraz w czasie prowadzenia akcji ratowniczej,
            <br />
            b. przebywa w stanie wskazujcym na stan nietrzewoci lub pod
            wpywem rodka odurzajcego,
            <br />
            c. wnosi i spoywa rodkw odurzajcych oraz napojw alkoholowych,
            zawyjtkiem napojw z obnion zawartoci alkoholu zakupionych na
            terenie obiektu.
            <br />
            d. uywa strojw i przedmiotw w sposb widoczny zabrudzonych lub
            ktre mog wchodzi w reakcje chemiczne z wod,
            <br />
            e. nurkowa oraz skaka do wody bez zgody lub wbrew zakazowi
            ratownika,
            <br />
            f. wnosi opakowa szklanych, puszek, ostrych narzdzi, broni oraz
            innych niebezpiecznych przedmiotw,
            <br />
            g. biega, popycha i wrzuca do wody, krzycze oraz zachowywa si
            w sposb zagraajcy bezpieczestwu wasnemu oraz innych
            uytkownikw obiektu,
            <br />
            h. wszczyna faszywych alarmw,
            <br />
            i. wrzuca do wody jakichkolwiek przedmiotw.
          </p>
        </li>
        <li>
          <p>
            Osoby niszczce lub uszkadzajce wyposaenie lub urzdzenia obiektu
            ponosz pen odpowiedzialno materialn za wyrzdzone szkody.
          </p>
        </li>
        <li>
          <p>Uwagi i wnioski naley zgasza w punkcie informacji.</p>
        </li>
        <li>
          <p>
            O innych sprawach nieobjtych niniejszym Regulaminem decyduj
            przepisy oglne oraz kierownictwo obiektu.
          </p>
        </li>
        <li>
          <p>
            Aquapark nie ponosi odpowiedzialnoci za jakiekolwiek zdarzenia
            wynikez nie przestrzegania niniejszego regulaminu.
          </p>
        </li>
        <li>
          <p>
            Podstaw prawn przetwarzania danych osobowych stanowi art. 6 ust. 1
            lit. b oglnego rozporzdzenia o ochronie danych osobowych (RODO) 
            przetwarzanie jest konieczne do wykonywania umowy (wiadczenia
            usug).Osobom, ktrych dane s przetwarzane, przysuguje prawo
            dostpu do nich, i ich poprawiania, usuwania, sprzeciwu wobec ich
            przetwarzania oraz dania ich przeniesienia do innego
            administratora danych, a take prawo wniesienia skargi do organu
            nadzorczego  Prezesa Urzdu Ochrony Danych Osobowych. Dane
            przetwarzane bd przez okres niezbdny do wykonania usugi. Kontakt
            z administratorem danych osobowych jest moliwy pod numerem telefonu
            22 222 22 22.
          </p>
        </li>
      </ul>
    </Modal>
  );
};
export default AgreementsModal;

AgreementsModal.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired
};
